
* {
  margin: 0;
  padding: 0;
  border: 0;
  min-height: 0;
  min-width: 0;
}
*:focus {
  outline: none;
}
body {
  overflow: hidden;
  background: #000;
}

.home ul {
  list-style-type: none;
  padding: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  height: 60px;
  width: 60px;
  -webkit-transition: 0.1s 0.1s ease-in;
  transition: 0.1s 0.1s ease-in;
}
.home ul li {
    position: absolute;
    height: 60px;
    width: 60px;
    color: #fff;
    text-align: center;
    font-size: 14px;
    line-height: 20px;
}
.home ul li.hover {
