.set-warp {
  z-index: 10;
  position: fixed;
  top: 20px;
  width: 250px;
  color: #fff;
}
.set-warp .title {
    padding-left: 20px;
    font-size: 36px;
    color: #fff;
    opacity: 0.9;
}
.set-warp .about-me {
    padding-left: 20px;
}
.set-warp .about-me span {
      font-size: 12px;
      color: #666;
      opacity: 0.5;
}
.set-warp .about-me a {
      font-size: 14px;
      color: #666;
      opacity: 0.6;
}
.set-warp .btn-list2, .set-warp .btn-list {
    position: absolute;
    padding: 10px 0px 10px 15px;
    left: 40px;
    opacity: 0.6;
    margin-bottom: 10px;
}
.set-warp .btn-list2 span, .set-warp .btn-list span {
      font-size: 28px;
      margin-right: 10px;
}
.set-warp .btn-list {
    left: 0px;
}
.set-warp .btn-list:hover ~ .content {
      margin-left: 0px;
}
.set-warp .content {
    position: absolute;
    height: 300px;
    width: 250px;
    padding: 10px;
    margin-top: 40px;
    margin-left: -272px;
    -webkit-transition: margin-left 0.9s;
    transition: margin-left 0.9s;
    font-size: 14px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.1);
}
.set-warp .content .set-com .set-title {
      opacity: 0.9;
      padding: 5px;
}
.set-warp .content .set-com .set-list {
      margin: 15px 0 15px 0;
}
.set-warp .content .set-com .set-list label {
        padding: 5px;
        opacity: 0.7;
}
.set-warp .content .set-com .set-list span {
        margin-left: 5px;
        opacity: 0.5;
}
.set-warp .content:hover {
      margin-left: 0px;
}
.set-warp .content:hover ~ .btn-list2, .set-warp .content:hover ~ .btn-list {
      right: 0px;
}
